      opacity: 0;
      transform: translateX(-20px);
      transition: all 0.5s ease-in-out;
    }

    .contentTransitionRight {
      opacity: 0;
      transform: translateX(40px);
      transition: all 0.5s ease-in-out;
    }

    .is-showing-right {
      opacity: 1 ;
      transform: translateX(-20px);
    }

    .is-showing {
      opacity: 1 ;
      transform: translateX(10px);
    }
